	module.exports = {"data":{"post":{"html":"<p>We're now a month into the process of registering Christopher for kindergarten, and we are more confused than ever. As you recall, he is behind in his speech development, and it has been recommended to us several times that he not join a regular kindergarten group. There is a local 'integrated' group that includes sessions of speech therapy each week, and there is a 'therapy' group in the county seat Winsen (over 30 km away) that is intensive speech therapy. However, our speech therapist today told us to choose carefully... that the therapy group does not necessarily include more therapy than an integrated group. In any case, the diagnosis has to be confirmed by the county health office, and our appointment there is not until June.</p>\n<p>Either group will be half-days, and will not be compatible with 2 full time working parents. I've informed my employer of the situation, and that I'll be either requesting part-time or leaving completely (and going freelance), but until we have more details, I can't make any decision.</p>","fields":{"title":"Up In The Air","date":"10 May 2004","url":"https://papascott.de/2004-05-10-up-in-the-air/"}}},"pathContext":{"slug":"/2004-05-10-up-in-the-air/","prev":{"fields":{"title":"","date":"10 May 2004","slug":"/2004-05-10-ongoing-torture/"}},"next":{"fields":{"title":"Useless Spiegel","date":"10 May 2004","slug":"/2004-05-10-useless-spiegel/"}}}}

